import { Fragment, Suspense } from 'react';
import classNames from 'classnames';
import { Transition } from 'react-transition-group';
import Link from 'components/Link';
import { Button } from 'components/Button';
import DecoderText from 'components/DecoderText';
import Divider from 'components/Divider';
import Section from 'components/Section';
import Heading from 'components/Heading';
import Text from 'components/Text';
import { ReactComponent as KatakanaProfile } from 'assets/katakana-profile.svg';
import profile from 'assets/profile2.png';
import { reflow } from 'utils/transition';
import prerender from 'utils/prerender';
import './Profile.css';

// const Portrait = lazy(() => import('components/Portrait'));

const ProfileText = ({ status, titleId }) => (
  <Fragment>
    <Heading
      className={classNames('profile__title', `profile__title--${status}`)}
      level={3}
      id={titleId}
    >
      <DecoderText text="A little about me," start={status !== 'exited'} delay={500} />
    </Heading>
    <Text
      className={classNames('profile__description', `profile__description--${status}`)}
      size="l"
    >
      I'm Saurabh, I work as frontend developer at{' '}
      <Link href="https://www.emtecinc.com">Emtec</Link>. I'm Ex-President of RIT India ð§
      Linux Users' Group, Red Hat Certified Engineer, Red Hat Certified System
      Administrator. So devops interests me. I work on various open source technologies
      and support it.
    </Text>
    <Text
      className={classNames('profile__description', `profile__description--${status}`)}
      size="l"
    >
      In my free time I like to watch movies (Sci-Fi, thriller, horror are my favorite
      generes). <br />I like to ride, I own a KTM duke ð.
      {/* I have a bit of experience in the cloud. Worked on many cloud platforms like AWS,
      Azure, OpenFaas, and Aneka. Successfully done a CI-CD for Angular and C# based
      project over Azure DevOps and worked on GitLab's CI for many projects. Worked some
      complex utilities written in python. I am known for my quick understanding, fast and
      effective implementation of problems. Full-stack with Angular and React as frontend
      frameworks as well Python(Flask), Java(Spring boot) for backend development. */}
    </Text>
  </Fragment>
);

const Profile = ({ id, visible, sectionRef }) => {
  const titleId = `${id}-title`;

  return (
    <Section
      className="profile"
      as="section"
      id={id}
      ref={sectionRef}
      aria-labelledby={titleId}
      tabIndex={-1}
    >
      <Transition in={visible} timeout={0} onEnter={reflow}>
        {status => (
          <div className="profile__content">
            <div className="profile__column">
              <ProfileText status={status} titleId={titleId} />
              {/* <Button
                secondary
                className={classNames('profile__button', `profile__button--${status}`)}
                href="/contact"
                icon="send"
              >
                Send me a message
              </Button> */}
            </div>
            <div className="profile__column" style={{ display: 'none' }}>
              <div className="profile__tag" aria-hidden>
                <Divider
                  notchWidth="64px"
                  notchHeight="8px"
                  collapsed={status !== 'entered'}
                  collapseDelay={1000}
                />
                <div
                  className={classNames(
                    'profile__tag-text',
                    `profile__tag-text--${status}`
                  )}
                >
                  About Me
                </div>
              </div>
              <div className="profile__image-wrapper">
                {!prerender && (
                  <Suspense fallback={null}>
                    <img
                      src={profile}
                      className={classNames(
                        'profile__image',
                        `profile__image--${status}`
                      )}
                      alt="profile"
                      // delay={100}
                    />
                  </Suspense>
                )}
                <KatakanaProfile
                  className={classNames('profile__svg', `profile__svg--${status}`)}
                />
              </div>
            </div>
          </div>
        )}
      </Transition>
    </Section>
  );
};

export default Profile;
